.certificate {
  padding: 120px 0 90px 0;
  position: relative;
  background: var(--light-color);
  &__container {
    display: flex;
    justify-content: space-between;
    position: relative;
  }
  &__info {
    max-width: 392px;
  }
  &__head {
    margin: 0;
    font-weight: 700;
    font-size: 16px;
    line-height: 150%;
    letter-spacing: 1px;
    text-transform: uppercase;
    color: var(--gray-color900);
    margin-bottom: 8px;
  }
  &__title {
    margin: 0;
    font-weight: 900;
    font-size: 46px;
    line-height: 130%;
    color: var(--gray-color900);
    margin-bottom: 48px;
  }
  &__desc {
    margin: 0;
    font-weight: 400;
    font-size: 16px;
    line-height: 160%;
    margin-bottom: 15px;
  }
  &__list {
    display: flex;
    gap: 15px;
  }
}
.certificate-item {
  position: absolute;
  right: 0;
  transform: translateY(20px);
}
@include tablet{
  .certificate {
    padding: 50px 0 20px 0;
  }
  .certificate__container {
    align-items: center;
    justify-content: center;
    flex-direction: column;
  }
  .certificate-item {
    position: static;
    transform: translateY(0px);
    margin-top: 50px;
  }
  .certificate__info {
    max-width: 535px;
    text-align: center;
  }
  .certificate__list {
    justify-content: center;
  }
}
@include mobile {
  .certificate__head {
    font-size: 11px;
  }
  .certificate__title {
    font-size: 30px;
  }
}
