.testimonials {
  padding: 50px 0;
  background: #f4f5f7;
  &__container {
    position: relative;
  }
  &__header {
    text-align: center;
    margin-bottom: 60px;
  }
  &__head {
    margin: 0;
    font-weight: 700;
    font-size: 16px;
    line-height: 150%;
    text-align: center;
    letter-spacing: 1px;
    text-transform: uppercase;
    color: var(--gray-color900);
    margin-bottom: 8px;
  }
  &__title {
    margin: 0;
    font-weight: 900;
    font-size: 46px;
    line-height: 130%;
    color: var(--gray-color900);
  }
  &__content {
    max-width: 1020px;
  }
  &__slide {
    &-item {
      background: var(--light-color);
      padding: 75px 0 75px 105px;
    }
    &-desc {
      margin: 0;
      max-width: 752px;
      font-weight: 400;
      font-size: 20px;
      line-height: 150%;
      color: var(--gray-color800);
      margin-bottom: 24px;
    }
    &-info {
      display: flex;
      align-items: center;
      gap: 20px;
    }
    &-name {
      margin: 0;
      font-weight: 700;
      font-size: 16px;
      line-height: 160%;
      color: var(--gray-color900);
    }
    &-position {
      margin: 0;
      font-weight: 400;
      font-size: 14px;
      line-height: 150%;
      color: var(--gray-color700);
    }
  }
  &__pagination {
    position: static;
    margin-top: 60px;
    width: 100%;
    .swiper-pagination-bullet {
      width: 30px;
      height: 5px;
      border-radius: 0;
    }
    .swiper-pagination-bullet-active {
      background: var(--gray-color900);
    }
  }
  .testimonials__content-btn {
    position: absolute;
    width: 100%;
    display: flex;
    justify-content: space-between;
    top: 50%;
    transform: translate(0, -50%);
  }
  .testimonials__slide-prev {
    padding: 18.5px 15px;
    border-radius: 50%;
    cursor: pointer;
    transition: 0.5s;
    &:hover {
      background: var(--danger-color);
      transition: 0.5s;
      svg {
        fill: white;
      }
    }
  }
  .testimonials__slide-next {
    padding: 18px 15px;
    border-radius: 45%;
    cursor: pointer;
    transition: 0.5s;
    svg{
      transform: rotate(178deg);
    }
    &:hover {
      background: var(--danger-color);
      transition: 0.5s;
      svg {
        fill: white;
      }
    }
  }
}
@include tablet {
  .testimonials__slide-item {
    padding: 50px 20px;
  }
  .testimonials .testimonials__content-btn{
    position: static;
    margin-top: 30px;
  }
}
@include mobile {
  .testimonials__slide-desc {
    font-size: 15px;
  }
}
