.team {
  padding: 150px 0 50px 0;
  background: #f4f5f7;
  overflow: hidden;
  &__header {
    margin-bottom: 60px;
  }
  &__head {
    margin: 0;
    font-weight: 700;
    font-size: 16px;
    line-height: 150%;
    letter-spacing: 1px;
    text-transform: uppercase;
    color: var(--gray-color900);
    margin-bottom: 8px;
  }
  &__title {
    margin: 0;
    font-weight: 900;
    font-size: 46px;
    line-height: 130%;
    color: var(--gray-color900);
  }
  &__card {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    gap: 30px;
    &-body {
      grid-column: 3 span;
    }
    &-img {
      position: relative;
      display: flex;
      justify-content: center;
      background: #ffcf2d;
      border-radius: 4px;
      margin-bottom: 16px;
      &:hover .team__card-social {
        opacity: 1;
      }
    }
    &-social {
      transition: 0.5s;
      opacity: 0;
      position: absolute;
      width: 100%;
      height: 100%;
      bottom: 0;
      display: flex;
      align-items: flex-end;
      justify-content: flex-end;
      gap: 17px;
      padding-right: 20px;
      padding-bottom: 20px;
      width: 100%;
      background: linear-gradient(
        180deg,
        rgba(30, 33, 44, 0) 43.9%,
        rgba(30, 33, 44, 0.03) 55.21%,
        rgba(30, 33, 44, 0.33) 68.73%,
        rgba(30, 33, 44, 0.56) 76.17%,
        rgba(30, 33, 44, 0.768225) 83.75%,
        rgba(30, 33, 44, 0.92) 91.49%,
        #1e212c 100%
      );
      &__list {
        display: flex;
        gap: 20px;
      }

      &__link {
        transition: 0.5s;
        &:hover {
          svg {
            transition: 0.5s;
            opacity: 1;
          }
        }
      }
    }
    &-info {
      text-align: center;
    }
    &-name {
      font-weight: 700;
      font-size: 20px;
      line-height: 150%;
      color: var(--gray-color900);
    }
    &-position {
      font-weight: 400;
      font-size: 16px;
      line-height: 160%;
      color: var(--gray-color700);
    }
  }
}
@include tablet {
  .team {
    padding: 50px 0 50px 0;
  }
  .team__card-body {
    grid-column: 6 span;
  }
}
@include mobile {
  .team__card {
    gap: 0;
    row-gap: 30px;
  }
  .team__card-body {
    grid-column: 12 span;
  }
}
