.subscribe {
  background: #ffdab9;
  position: relative;
  padding: 120px 0;
  &__container {
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    max-width: 810px;
  }
  &__head {
    margin: 0;
    font-weight: 700;
    font-size: 16px;
    line-height: 150%;
    text-align: center;
    letter-spacing: 1px;
    text-transform: uppercase;
    color: var(--gray-color900);
    margin-bottom: 8px;
  }
  &__title {
    margin: 0;
    font-weight: 900;
    font-size: 46px;
    line-height: 130%;
    text-align: center;
    color: var(--gray-color900);
    margin-bottom: 60px;
  }
  &__form {
    display: flex;
    justify-content: center;
    width: 100%;
    gap: 25px;
  }
  &__input {
    background: var(--light-color);
    border: 1px solid #d7dadd;
    border-radius: 4px;
    padding: 12px 16px;
    max-width: 452px;
    width: 100%;
    font-weight: 400;
    font-size: 16px;
    line-height: 160%;
    color: var(--gray-color900);
    &:focus {
      outline: none;
    }
    &::placeholder {
      font-weight: 400;
      font-size: 16px;
      line-height: 160%;
      color: var(--gray-color700);
    }
  }
  &__btn {
    cursor: pointer;
    padding: 0 40px;
    font-weight: 700;
    font-size: 16px;
    line-height: 52px;
    color: var(--danger-color);
    border-radius: 4px;
    border: 1px solid #ff4242;
    background: var(--light-color);
    transition: 0.5s;
    &:hover {
      color: var(--light-color);
      background: var(--btn-gradient);
      transition: 0.3s;
    }
  }
  &__decor {
    &-1 {
      position: absolute;
      bottom: 0;
      left: 0;
    }
    &-2 {
      position: absolute;
      bottom: 0;
      right: 0;
    }
  }
}
@include big-desktop {
  .subscribe__decor-1,
  .subscribe__decor-2 {
    width: 250px;
  }
}
@include tablet {
  .subscribe{
    padding: 50px 0;
  }
  .subscribe__decor-1,
  .subscribe__decor-2 {
    width: 150px;
  }
}
@include mobile {
  .subscribe__title {
    font-size: 39px;
  }
  .subscribe__form {
    flex-direction: column;
  }
  .subscribe__decor-1,
  .subscribe__decor-2 {
    display: none;
  }
  .subscribe__input {
    max-width: none;
  }
}
