.blog {
  padding: 120px 0;
  &__header {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    gap: 30px;
    justify-content: space-between;
    margin-bottom: 60px;
  }
  &__head {
    margin: 0;
    font-weight: 700;
    font-size: 16px;
    line-height: 150%;
    color: var(--gray-color900);
    margin-bottom: 8px;
  }
  &__title {
    margin: 0;
    font-weight: 900;
    font-size: 46px;
    line-height: 130%;
    color: var(--gray-color900);
  }
  &__btn {
    display: block;
    padding: 0 40px;
    font-weight: 700;
    font-size: 16px;
    line-height: 52px;
    color: var(--danger-color);
    border-radius: 4px;
    border: 1px solid #ff4242;
    background: var(--light-color);
    transition: 0.5s;
    &:hover {
      color: var(--light-color);
      background: var(--btn-gradient);
      transition: 0.3s;
    }
  }
  &__card {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    grid-gap: 30px;
    &-body {
      grid-column: 4 span;
    }
    &-header {
      position: relative;
      img {
        width: 100%;
      }
      &__info {
        position: absolute;
        top: 12px;
        left: 12px;
        cursor: pointer;
        background: #ffffff;
        border-radius: 4px;
        padding: 0 10px;
        font-weight: 400;
        font-size: 14px;
        line-height: 150%;
        color: var(--gray-color800);
      }
    }
    &-info {
      display: flex;
      gap: 12px;
      margin-bottom: 8px;
      &__item {
        display: flex;
        align-items: center;
        font-weight: 400;
        font-size: 14px;
        line-height: 150%;
        color: var(--gray-color700);
        &:nth-child(1)::after {
          content: "|";
          margin-left: 8px;
        }
        &:nth-child(2)::before {
          content: url(../img/svg/calendar.svg);
          margin-right: 8px;
        }
        &:nth-child(2)::after {
          content: "|";
          margin-left: 8px;
        }
        &:nth-child(3)::before {
          content: url(../img/svg/clock.svg);
          margin-right: 8px;
        }
      }
    }
    &-title {
      margin: 0;
      font-weight: 700;
      font-size: 20px;
      line-height: 150%;
      color: var(--gray-color900);
      margin-bottom: 8px;
    }
    &-desc {
      display: flex;
      flex-direction: column;
    }
    &-text {
      margin: 0;
      font-weight: 400;
      font-size: 16px;
      line-height: 160%;
      color: var(--gray-color800);
      margin-bottom: auto;
    }
    &-link {
      margin-top: 16px;
      display: flex;
      align-items: center;
      font-weight: 700;
      font-size: 16px;
      line-height: 160%;
      color: var(--gray-color900);
      &::after {
        content: url(../img/svg/right.svg);
        margin-left: 8px;
        position: relative;
        top: 5px;
      }
    }
  }
}
@include tablet {
  .blog{
    padding: 50px 0;
  }
  .blog__card-body {
    grid-column: 6 span;
  }
}
@include small-tablet {
  .blog__card {
    gap: 0;
    row-gap: 30px;
  }
  .blog__card-body {
    grid-column: 12 span;
    gap: 0;
    row-gap: 30px;
  }
  .blog__card-info__item {
    font-size: 11px;
  }

}
